// Imports
import * as fs from 'fs';
import * as os from 'os';
import * as path from 'path';
import stripJsonComments = require('strip-json-comments');
import Validator = require('z-schema');
import * as yargs from 'yargs';
import * as gulp from 'gulp';

import { initializeTslintTask } from '@microsoft/sp-tslint-rules';

import {
  IExecutable,
  task,
  serial,
  parallel,
  watch,
  CopyTask,
  setConfig
} from 'gulp-core-build';
import * as gulpCoreBuild from 'gulp-core-build';
import { typescript, tslint, text, removeTripleSlash } from 'gulp-core-build-typescript';
import sass from 'gulp-core-build-sass';
import { serve, reload } from 'gulp-core-build-serve';
import webpack from 'gulp-core-build-webpack';
import karma from 'gulp-core-build-karma';
import * as spBuildCoreTasks from '@microsoft/sp-build-core-tasks';

// Exports
export * from 'gulp-core-build';
export * from 'gulp-core-build-typescript';
export * from 'gulp-core-build-sass';
export * from 'gulp-core-build-serve';
export * from 'gulp-core-build-webpack';
export * from 'gulp-core-build-karma';
export * from '@microsoft/sp-build-core-tasks';

export const preCopy: CopyTask = new CopyTask();
export const postCopy: CopyTask = new CopyTask();
preCopy.name = 'pre-copy';
postCopy.name = 'post-copy';

const enableCasperTests: boolean = true;

const schemaKey: string = '$schema';

const testTaskName: string = 'test';
const buildTaskName: string = 'build';
const bundleTaskName: string = 'bundle';
const deployAzureStorageTaskName: string = 'deploy-azure-storage';
const packageSolutionTaskName: string = 'package-solution';
const serveTaskName: string = 'serve';
const defaultTaskName: string = 'default';

const debugBuildMode: string = 'debug';
const shipBuildMode: string = 'ship';

export interface IMsCoreBuildArgs {
  production: boolean;
  ship: boolean;
  locale: string;
  verbose: boolean;
}

export let args: IMsCoreBuildArgs;

export function setBuildProperties(properties: { [key: string]: any }): void {
  setConfig({ properties: properties });
}

const additionalBuildTasks: IExecutable[] = [];

/**
 * Add additional build tasks to run after the typescript tasks.
 */
export function addBuildTasks(tasks: IExecutable | IExecutable[]): void {
  if ((tasks as IExecutable[]).length) {
    additionalBuildTasks.push.call(this, tasks);
  } else {
    additionalBuildTasks.push(tasks as IExecutable);
  }
}

export interface IConfigDefinition {
  configFile: string;
  schemaFile: string;
  readmeUrl?: string;
  callback: (config: any) => void;
}

function getSchemaFilePath(filename: string): string {
  return path.join(__dirname, 'schemas', filename);
}

export class SpWebBuild {
  protected customConfigs: Array<IConfigDefinition> = [
    {
      callback: setBuildProperties,
      configFile: 'config.json',
      schemaFile: getSchemaFilePath('config.schema.json')
    },
    {
      callback: serve.setConfig.bind(serve),
      configFile: 'serve.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-serve#servetask',
      schemaFile: undefined
    },
    {
      callback: spBuildCoreTasks.deployAzureStorage.setConfig.bind(spBuildCoreTasks.deployAzureStorage),
      configFile: 'deploy-azure-storage.json',
      readmeUrl: 'https://github.com/OfficeDev/SharePointUXFramework/wiki/' +
        'Notes-on-Azure-Deployment#configuration',
      schemaFile: undefined
    },
    {
      callback: spBuildCoreTasks.packageSolution.setConfig.bind(spBuildCoreTasks.packageSolution),
      configFile: 'package-solution.json',
      readmeUrl:
        'https://github.com/OfficeDev/SharePointUXFramework/wiki/Notes-on-Solution-Packaging#packaging',
      schemaFile: undefined
    },
    {
      callback: preCopy.setConfig.bind(preCopy),
      configFile: 'pre-copy.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build/blob/master/src/CopyTask.ts',
      schemaFile: getSchemaFilePath('copy.schema.json')
    },
    {
      callback: postCopy.setConfig.bind(postCopy),
      configFile: 'post-copy.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build/blob/master/src/CopyTask.ts',
      schemaFile: getSchemaFilePath('copy.schema.json')
    },
    {
      callback: typescript.setConfig.bind(typescript),
      configFile: 'typescript.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-typescript/blob/master/src/TypeScriptTask.ts',
      schemaFile: undefined
    },
    {
      callback: text.setConfig.bind(text),
      configFile: 'text.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-typescript/blob/master/src/TextTask.ts',
      schemaFile: undefined
    },
    {
      callback: tslint.setConfig.bind(tslint),
      configFile: 'tslint.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-typescript/blob/master/src/TSLintTask.ts',
      schemaFile: undefined
    },
    {
      callback: sass.setConfig.bind(sass),
      configFile: 'sass.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-sass/blob/master/src/SassTask.ts',
      schemaFile: undefined
    },
    {
      callback: reload.setConfig.bind(reload),
      configFile: 'reload.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-serve/blob/master/src/ReloadTask.ts',
      schemaFile: undefined
    },
    {
      callback: webpack.setConfig.bind(webpack),
      configFile: 'webpack.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-webpack/blob/master/src/WebpackTask.ts',
      schemaFile: undefined
    },
    {
      callback: karma.setConfig.bind(karma),
      configFile: 'karma.json',
      readmeUrl: 'https://github.com/dzearing/gulp-core-build-karma/blob/master/src/KarmaTask.ts',
      schemaFile: undefined
    },
    {
      callback: spBuildCoreTasks.configureWebpack.setConfig.bind(spBuildCoreTasks.configureWebpack),
      configFile: 'configure-webpack.json',
      readmeUrl: 'https://onedrive.visualstudio.com/DefaultCollection/SPPPlat/_git/sp-build-core-tasks?path=%2' +
        'Fsrc%2FconfigureWebpack%2FConfigureWebpackTask.ts&version=GBmaster&fullScreen=true&_a=contents',
      schemaFile: undefined
    },
    {
      callback: spBuildCoreTasks.copyAssets.setConfig.bind(spBuildCoreTasks.copyAssets),
      configFile: 'prepare-deploy.json',
      readmeUrl: 'https://onedrive.visualstudio.com/DefaultCollection/SPPPlat/_git/sp-build-core-tasks?path=%2' +
        'Fsrc%2FprepareDeploy%2FPrepareDeployTask.ts&version=GBmaster&_a=contents',
      schemaFile: undefined
    },
    {
      callback: spBuildCoreTasks.writeManifests.setConfig.bind(spBuildCoreTasks.writeManifests),
      configFile: 'write-manifests.json',
      readmeUrl: 'https://onedrive.visualstudio.com/DefaultCollection/SPPPlat/_git/sp-build-core-tasks?path=%2' +
        'Fsrc%2FwriteManifests%2FWriteManifestsTask.ts&version=GBmaster&_a=contents',
      schemaFile: undefined
    },
    {
      callback: spBuildCoreTasks.copyStaticAssets.setConfig.bind(spBuildCoreTasks.copyStaticAssets),
      configFile: 'copy-static-assets.json',
      readmeUrl: 'https://onedrive.visualstudio.com/DefaultCollection/SPPPlat/_git/sp-build-core-tasks?path=%2' +
        'Fsrc%2FcopyStaticAssets%2FCopyStaticAssetsTask.ts&version=GBmaster&_a=contents',
      schemaFile: getSchemaFilePath('copy-static-assets.schema.json')
    }
  ];

  private _schemaValidator: ZSchema.Validator = new Validator({
    breakOnFirstError: true,
    noExtraKeywords: true,
    noTypeless: true
  });

  public initialize(gulp: gulp.Gulp): void {
    const yargsArgs: yargs.Argv = this.getYargs();
    const command: string = yargsArgs.argv._[0];

    switch (command) {
      case testTaskName:
        yargsArgs.reset()
          .option('debug', {
            describe: 'run tests in debug mode'
          })
          .option('match', {
            describe: 'regular expression. Only run tests that match',
            string: true
          });
        break;

      case serveTaskName:
        yargsArgs.reset()
          .option('showToast', {
            description: 'show a toast when the watched code fails a build or succeeds after failing'
          })
          .option('nobrowser', {
            description: 'don\'t open a browser after initial bundle'
          });
        break;

      default:
        yargsArgs.reset(); // These commands have no special options.
        break;
    }

    yargsArgs.usage([
        'CONFIG FILES'
      ].concat(
        this.customConfigs.map((config: IConfigDefinition) => {
          return `   ${config.configFile}${os.EOL}${config.readmeUrl ? config.readmeUrl : ''}${os.EOL}`;
        }
      ))
      .join(os.EOL));

    args = yargsArgs.argv;

    // Note this overrides the getters for ship and production on args
    args.ship = args.production = (args.production || args.ship);

    console.log('Build target: ' + (args.ship ? shipBuildMode.toUpperCase() : debugBuildMode.toUpperCase()));
    gulpCoreBuild.setConfig({
      production: args.production
    });

    const tasks: Map<string, IExecutable> = this.getTasks();
    tasks.forEach((executable: IExecutable, name: string): void => {
      task(name, executable);
    });

    this.setupSharedConfig(args);
    this._loadCustomConfig(args);

    gulpCoreBuild.initialize(gulp);
  }

  protected getYargs(): yargs.Argv {
    const yargsArgs: yargs.Argv = yargs.usage()
      .option('production', {
        alias: 'p',
        describe: 'build in ship mode with full localization and minimization',
        boolean: true
      })
      .option('ship', {
        alias: 'p',
        describe: 'build in ship mode with full localization and minimization',
        boolean: true
      })
      .option('locale', {
        alias: 'l',
        describe: 'override the default culture (e.g. "fr-fr")',
        string: true
      })
      .option('verbose', {
        describe: 'run the build with verbose logging'
      })
      .help('h')
      .global(['production', 'locale', 'verbose', 'h'])
      .command(buildTaskName, 'build the project')
      .command(bundleTaskName, 'build, localize, and bundle the project')
      .command(deployAzureStorageTaskName, 'upload the assets to a development CDN')
      .command(packageSolutionTaskName, 'package the project into a SPAPP')
      .command(testTaskName, 'build, localize, and bundle the project and run tests, and verify the coverage')
      .command(serveTaskName, 'build and bundle the project and run the development server')
      .command(defaultTaskName, 'equivalent to bundle');

    return yargsArgs;
  }

  /**
   * Define default task groups.
   */
  protected getTasks(): Map<string, IExecutable> {
    const result: Map<string, IExecutable> = new Map<string, IExecutable>();

    result.set(buildTaskName, this.getBuildTask());

    result.set(bundleTaskName, this.getBundleTask());

    result.set(testTaskName, this.getTestTask());

    result.set(deployAzureStorageTaskName, spBuildCoreTasks.deployAzureStorage as any);
    result.set(packageSolutionTaskName, spBuildCoreTasks.packageSolution as any);

    // @todo VSO #167343 - remove this once bug from running 'gulp bundle test' in gulp-core-build is fixed
    result.set(testTaskName, this.getTestTask());

    result.set(serveTaskName,
               serial(result.get(bundleTaskName),
                      serve,
                      watch([ 'src/**/*.{ts,tsx,scss,resx,js,txt,html}',
                              '!src/**/*.{scss.ts,resx.ts}'],
                            serial(result.get(bundleTaskName), reload))));

    result.set(defaultTaskName, result.get(bundleTaskName));

    return result;
  }

  protected getTestTask(): IExecutable {
    return serial(this.getBundleTask(),
                  enableCasperTests
                    ? parallel(spBuildCoreTasks.casperJSRunner as any, karma)
                    : karma);
  }

  protected getCoreBuildTask(): IExecutable {
    return parallel(tslint,
                    serial(sass,
                            serial(typescript, removeTripleSlash),
                            serial(...additionalBuildTasks)),
                      spBuildCoreTasks.copyStaticAssets as any,
                    text);
  }

  protected getBuildTask(): IExecutable {
    return serial(preCopy,
                  this.getCoreBuildTask(),
                  postCopy);
  }

  protected getBundleTask(): IExecutable {
    return serial(this.getBuildTask(),
                  spBuildCoreTasks.configureWebpack as any,
                  webpack,
                  spBuildCoreTasks.prepareDeploy  as any,
                  spBuildCoreTasks.writeManifests as any);
  }

  protected setupSharedConfig(buildArgs: IMsCoreBuildArgs): void {
    initializeTslintTask(tslint as any);

    sass.setConfig({
      dropCssFiles: true  // Drops .css files in the lib directory for webpack
    });

    karma.setConfig({
      karmaConfigPath: path.resolve(path.join(__dirname, '..', 'karma.config.js'))
    });

    spBuildCoreTasks.configureWebpack.setConfig({
      webpack: (webpack as any)
    });

    const branchType: string = (process.env.BranchType || '').toLowerCase();
    spBuildCoreTasks.writeManifests.setConfig({
      cdnBasePath: (branchType === 'prod')
        ? 'https://spoprod-a.akamaihd.net/files/'
        : 'https://spoppe-a.akamaihd.net/files/',
      debugBasePath: (): string => `http://${os.hostname() || 'localhost'}:${serve.taskConfig.port}/`,
      debugLocale: buildArgs.locale,
      deployCdnPath: (): string => spBuildCoreTasks.copyAssets.taskConfig.deployCdnPath
    });

    if (buildArgs.production) {
      let buildNumber: string = undefined;
      // When the build runs on VSTS, the build number is included as part of the CDN path, so we
      //   need to pass it in here for public path references. This should change in the future.
      if (process.env.BUILD_BUILDNUMBER) {
        buildNumber = process.env.BUILD_BUILDNUMBER.toLowerCase();
      }

      spBuildCoreTasks.copyAssets.setConfig({
        extsToIgnore: ['.map', '.stats.json', '.stats.html']
      });

      spBuildCoreTasks.writeManifests.setConfig({
        cdnUrlPrefix: buildNumber
      });

      spBuildCoreTasks.packageSolution.setConfig({
        paths: {
          manifestDir: (): string => spBuildCoreTasks.copyAssets.taskConfig.deployCdnPath
        }
      });
    }
  }

  private _loadCustomConfig(buildArgs: IMsCoreBuildArgs): void {
    // Read config files from project folders for various projects
    // @todo VSO #178074 - move this into each task, respectively

    // Load config file for each plugin
    for (const taskConfigInfo of this.customConfigs) {
      const configFilename: string = path.join(process.cwd(), 'config', taskConfigInfo.configFile);
      const schemaFilename: string = taskConfigInfo.schemaFile;
      const rawConfig: {} = this._readConfigFile(buildArgs, configFilename, schemaFilename, taskConfigInfo.readmeUrl);
      if (rawConfig) {
        taskConfigInfo.callback(rawConfig);
      }
    }
  }

  private _readConfigFile(buildArgs: IMsCoreBuildArgs, filename: string, schemafile: string, helpUrl: string): Object {
    if (!fs.existsSync(filename)) {
      return undefined;
    } else {
      if (buildArgs.verbose) {
        console.log(`Found config file: ${path.basename(filename)}`);
      }
      const contents: Buffer = fs.readFileSync(filename);
      const stripped: string = stripJsonComments(contents.toString());
      const rawConfig: string = JSON.parse(stripped);

      delete rawConfig[schemaKey];

      if (schemafile) {
        const schema: {} = require(schemafile);
        if (!this._schemaValidator.validate(rawConfig, schema)) {
          const error: ZSchema.SchemaError = this._schemaValidator.getLastError();
          const errorMessage: string =
            `Error parsing file '${path.basename(filename)}', section [${error.path}]:`
            + os.EOL + `(${error.code}) ${error.message} ` + os.EOL + os.EOL +
            (helpUrl ? 'For more usage, please read:' + os.EOL + helpUrl : '');

          console.log(os.EOL + 'ERROR: ' + errorMessage + os.EOL + os.EOL);
          throw new Error(errorMessage);
        }
      }

      return rawConfig;
    }
  }
}

// Replace gcb.initialize to call our init stuff first.
export const initialize: (gulp: gulp.Gulp) => void = (gulp: gulp.Gulp): void => {
  const build: SpWebBuild = new SpWebBuild();
  build.initialize(gulp);
};
